import { React }from "react";
import "../styles/Main.css";
import DynamicText from "./DynamicText";
import Boxes from "../img/cajas.png";
import ScrollAnimation from "react-animate-on-scroll";

function Main() {
    const textToRotate = [
        "Brindamos recursos de ingeniera y mantenimiento a equipos industriales.",
        "Garantizamos la atencin y calidad en cada trabajo.",
        "Nuestros valores: Excelencia, colaboracin, lealtad, responsabilidad y transparencia.",
        "Tenemos ms de 25 aos de experiencia en el rea.",
        "Ms de 100 clientes concurrentes nos respaldan."
    ];

    return (
        <div className="main">
            <div className="secondary-info">
                <p className="highlighted-text"><span><DynamicText toRotate={textToRotate} velocity={100} pauseTime={2000}/></span></p>
            </div>
            <div className="main-info">
                <p>Servicios de Ingeniera en Maquinaria y Montacargas.</p> 
            </div>
        </div>
    );
}

export default Main;